import React from "react";
import "./css/footer.css";

import MailIcon from "@material-ui/icons/Mail";
import FacebookIcon from "@material-ui/icons/Facebook";
import InstagramIcon from "@material-ui/icons/Instagram";
import PhoneIcon from "@material-ui/icons/Phone";

import Logo from "../../../assets/Images/white_logo.png";

function footer() {
    return (
        <div className="footerContainer">
            <div className="footerSecondContainer">
                <p className="footerDescription">
                       2006  .  
                           
                    ,      
                     ,     
                        , 
                    ,  ,,    
                      ,     
                     .
                </p>
                <div
                    style={{
                        display: "flex",
                        flexDirection: "row",
                        width: "58%",
                    }}
                >
                    <img src={Logo} alt="oim logo" className="footerLogo" />
                    <ul
                        style={{
                            width: "37%",
                            listStyleType: "none",
                        }}
                    >
                        <li>
                            <MailIcon
                                style={{ marginRight: 5, fill: "#82d900" }}
                            />
                            oimmgl@gmail.com
                        </li>
                        <li
                            onClick={() =>
                                window.open(
                                    `https://www.facebook.com/Oim.Nogoon.Urlan`,
                                    "_blank"
                                )
                            }
                        >
                            <FacebookIcon
                                style={{ marginRight: 5, fill: "#82d900" }}
                            />
                            
                        </li>
                        <li
                            onClick={() =>
                                window.open(
                                    `https://www.instagram.com/oim_nogoon_urlan/`,
                                    "_blank"
                                )
                            }
                        >
                            <InstagramIcon
                                style={{ marginRight: 5, fill: "#82d900" }}
                            />
                            oim_nogoon_urlan
                        </li>
                        <li>
                            <PhoneIcon
                                style={{ marginRight: 5, fill: "#82d900" }}
                            />
                            7777_3310
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    );
}

export default footer;
